:root{
  --bg: #fff7f8;
  --ink: #222;
  --muted: #6b7280;
  --rose: #e11d48;
  --card: #ffffff;
  --radius: 20px;
  --shadow: 0 10px 25px rgba(0,0,0,.08);
}

* { box-sizing: border-box; }
html, body, #root { height: 100%; }
body {
  margin: 0;
  font-family: 'Anta', system-ui, -apple-system, Segoe UI, Roboto, sans-serif;
  color: var(--ink);
  background: var(--bg);
}

.wrap { max-width: 1100px; margin: 0 auto; padding: 0 20px; }

/* Nav */
.nav {
  position: sticky; top: 0; z-index: 20;
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  background: rgba(255,255,255,.7);
  border-bottom: 1px solid #eee;
}
.nav .wrap {
  display: flex; align-items: center; justify-content: space-between;
  padding: 14px 20px;
}
.nav a { color: var(--ink); text-decoration: none; margin-left: 18px; }
.nav .brand { font-weight: 700; letter-spacing: .5px; }
.nav .btn.small {
  padding: 8px 14px; border-radius: 999px; border: 1px solid var(--rose); color: var(--rose);
}

/* Hero */
.hero {
  position: relative; height: 70vh; display: grid; place-items: center;
  background: url('https://images.unsplash.com/photo-1522673607200-164d1b6ce486?q=80&w=2400&auto=format&fit=crop') center/cover no-repeat;
}
.hero .overlay { position: absolute; inset: 0; background: rgba(0,0,0,.45); }
.hero .inner { position: relative; color: white; text-align: center; padding: 0 16px; }
.hero .kicker { text-transform: uppercase; letter-spacing: .3em; font-size: 12px; opacity: .9; }
.hero h1 { font-size: clamp(36px, 7vw, 64px); margin: .3em 0 .2em; }
.hero .meta { opacity: .9; margin-bottom: 18px; }

.btn {
  display: inline-block; background: var(--rose); color: white;
  padding: 12px 22px; border-radius: 16px; text-decoration: none;
  box-shadow: var(--shadow); transition: .2s transform ease;
}
.btn:hover { transform: translateY(-2px); }

.section { padding: 64px 0; }
.section h2 { font-size: clamp(24px, 4vw, 36px); margin: 0 0 16px; }

.cards { display: grid; grid-template-columns: repeat(auto-fit, minmax(220px,1fr)); gap: 18px; }
.card { background: var(--card); border: 1px solid #eee; border-radius: var(--radius); padding: 18px; box-shadow: var(--shadow); }
.ctitle { font-weight: 700; margin-bottom: 8px; }

.grid-2 { display: grid; grid-template-columns: 1fr; gap: 24px; }
@media (min-width: 900px) { .grid-2 { grid-template-columns: 1fr 1fr; } }

.timeline { list-style: none; padding: 0; margin: 0; display: grid; gap: 14px; }
.timeline li { display: flex; gap: 14px; align-items: flex-start; }
.timeline .time { width: 64px; text-align: right; font-weight: 600; margin-top: 8px; }
.box { background: var(--card); border: 1px solid #eee; border-radius: var(--radius); padding: 14px 16px; box-shadow: var(--shadow); }
.btitle { font-weight: 700; margin-bottom: 4px; }
.bdesc { color: var(--muted); font-size: 14px; }

.venue .map { margin: 12px 0; border-radius: 14px; overflow: hidden; border: 1px solid #eee; aspect-ratio: 4/3; }
.venue iframe { width: 100%; height: 100%; border: 0; }

.gallery { display: grid; grid-template-columns: repeat(2, 1fr); gap: 8px; }
@media (min-width: 900px) { .gallery { grid-template-columns: repeat(3, 1fr); } }
.gallery img { width: 100%; height: 200px; object-fit: cover; border-radius: 14px; }

.form { background: var(--card); border: 1px solid #eee; border-radius: var(--radius); padding: 18px; box-shadow: var(--shadow); display: grid; gap: 14px; }
.row { display: grid; grid-template-columns: 1fr; gap: 14px; }
@media (min-width: 800px) { .row { grid-template-columns: 1fr 1fr; } }
.field { display: grid; gap: 6px; }
.field label { font-size: 14px; color: var(--muted); }
.field input, .field select, .field textarea {
  width: 100%; border: 1px solid #ddd; border-radius: 12px; padding: 10px 12px; font: inherit;
}

.footer { border-top: 1px solid #eee; padding: 26px 0; color: var(--muted); text-align: center; }
.countdown { display: grid; grid-template-columns: repeat(4, 1fr); gap: 8px; max-width: 520px; margin: 16px auto 0; }
.count { background: rgba(255,255,255,.9); color: #111; border-radius: 16px; padding: 10px 8px; text-align: center; }
.count .v { font-weight: 700; font-size: 22px; }
.count .l { font-size: 11px; text-transform: uppercase; letter-spacing: .12em; opacity: .7; }
